set -1px 0 #bce8f1;-moz-box-shadow:inset -1px 0 #bce8f1;box-shadow:inset -1px 0 #bce8f1}table[data-sortable].sortable-theme-slick th[data-sorted=true]:last-child{-webkit-box-shadow:inset 1px 0 #bce8f1;-moz-box-shadow:inset 1px 0 #bce8f1;box-shadow:inset 1px 0 #bce8f1}table[data-sortable].sortable-theme-slick th[data-sorted=true][data-sorted-direction=descending]:after{border-top-color:#3a87ad}table[data-sortable].sortable-theme-slick th[data-sorted=true][data-sorted-direction=ascending]:after{border-bottom-color:#3a87ad}